:root {
    --base-color: #414152;
    --pale-base-color: #8A7D74;
    --accent-color: #FF7247;
    --satisfied-clients: #AD9382;
    --hover-color: #FF7247;
    --black: black; /* todo */
    --white: white;
    --line-color: #EBE4E0;
    --light-arrow-color: linear-gradient(0deg, #FBFBFB, #FBFBFB), linear-gradient(234.71deg, rgba(255, 255, 255, 0.45) 20.3%, rgba(255, 255, 255, 0) 93.43%);
    --main-padding: 80px;
    --main-mobile-padding: 20px;

    --relative-box-margin-bottom-mobile: 47px;
    --relative-box-margin-top-mobile: 33px;

    /* relative-box */
    --left-container-top: 162px; /* margin-top left-container (względem końca nagłówka)*/
    --right-container-top: 66px;
    --photographer-top: 80px;
    --photographer-top-mobile: 120px;
    --biggest-arrow-top: 280px;

    --biggest-arrow-width: 524px;
    --biggest-arrow-width-mobile: 255px;

    /* manipulacja względną pozycją największej strzałki dla ekranu < 900
    względem lewego, górnego wierzchołka zdjęcia fotografki. */
    --biggest-arrow-right-900: -180px;
    --biggest-arrow-top-900: 50px;

    --biggest-arrow-right-mobile: -90px;
    --biggest-arrow-top-mobile: 50px;

    --logo-width: 150px;
    --logo-mobile-width: 166px;
    --logo-margin: 66px;
    --logo-margin-mobile: 30px;
    --logo-margin-top: 45px;

    --photographer-width: 779px;
    --photographer-margin-right: 10px;
    --photographer-width-mobile: 90vw;


    --button-width: 208px;
    --button-height: 60px;
}